.App {
  transition: transform 2s ease-in;
}

#svgClarifier {
  animation: zoomIn 2s ease-in-out; 
}

@keyframes zoomIn {
  0% {
      transform: scale(0.5);
      -webkit-transform: scale(0.5);
      opacity: 0.5;
  }
  100% {
      transform: scale(1.0);
      -webkit-transform: scale(1.0);
      opacity: 1;

  }
}

.testHideShow{
  display:none
}

.display{
  display: initial
}